.events-gallery {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.image-wrapper {
    opacity:1;
    -ms-flex-positive: 1;
        flex-grow: 1;
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
    max-width: 400px;
    margin: 1em;
    -webkit-box-shadow: 3px 1px 5px 1px #888888;
            box-shadow: 3px 1px 5px 1px #888888;
    position:relative;
}

.image-wrapper span {
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    -ms-flex-direction:column;
        flex-direction:column;
    opacity:0;
    position:absolute;
    height:30%;
    width:0;
    background:#7df9b9;
    top:35%;
    left:25%;
    -webkit-box-shadow: 3px 1px 5px 1px #333;
            box-shadow: 3px 1px 5px 1px #333;
    text-align:center;
    color:#fff;
    font-weight:bold;
    -webkit-transition:all 0.3s ease-in-out;
    -o-transition:all 0.3s ease-in-out;
    transition:all 0.3s ease-in-out;
    font-family:"Morebi Rounded",Times,serif;
}
.image-wrapper:hover,.image-wrapper:focus,.image-wrapper:active {
    /*opacity:0.5;*/
}

.image-wrapper:hover span,.image-wrapper:focus span,.image-wrapper:active span{
    opacity:1;
    width:50%;
    cursor:pointer;
}
.events-gallery img {
    height: 100%;
    width: 100%;
}

.events-gallery-title {
    font-weight:500;
    font-size:xx-large;
    color:#666;
    font-family: "Morebi Rounded", Times, serif;
}

.event-break {
    width:50%;
    border: 0;
    height: 1px;
    background-image: -webkit-gradient(linear, left top, right top, from(rgba(0, 0, 0, 0)), color-stop(rgba(125, 249, 185, 0.75)), to(rgba(0, 0, 0, 0)));
    background-image: -webkit-linear-gradient(left, rgba(0, 0, 0, 0), rgba(125, 249, 185, 0.75), rgba(0, 0, 0, 0));
    background-image: -o-linear-gradient(left, rgba(0, 0, 0, 0), rgba(125, 249, 185, 0.75), rgba(0, 0, 0, 0));
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(125, 249, 185, 0.75), rgba(0, 0, 0, 0));
}

@media screen and (max-width: 767px) {
    .image-wrapper {
        -ms-flex-preferred-size: 40%;
            flex-basis: 40%;
        max-width:40%;
    }

}

@media screen and (max-width: 440px) {
    .image-wrapper {
        -ms-flex-preferred-size: 90%;
            flex-basis: 90%;
    }
}


